import React from 'react'
import {
  createButton,
  createSvgIcon,
  FacebookLoginButton,
  GithubLoginButton,
  GoogleLoginButton,
} from 'react-social-login-buttons'
import IconFacebook from '../../../assets/icone-facebook.svg'

export const FACEBOOK = 'facebook'
export const GITHUB = 'github'
export const GOOGLE = 'google'

export type ProviderProps = {
  loginUrl: string
  alias: string
  providerId: string
  displayName: string
}

export const SocialLoginButton = ({ provider }: { provider: ProviderProps }) => {
  const { providerId, loginUrl, displayName } = provider

  const config = {
    style: {
      display: 'flex',
      justifyContent: 'center',
      alignItems: 'center',
      height: 'unset',
      padding: '0.5rem',
      boxShadow: 'none',
      backgroundColor: '#fff',
      color: '#6639F4',
      border: '1px solid #6639F4',
      textTransform: 'uppercase',
      fontWeight: 600,
      fontSize: '0.875rem',
      borderRadius: '8px',
    },
    activeStyle: {
      display: 'flex',
      justifyContent: 'center',
      alignItems: 'center',
      height: 'unset',
      padding: '0.5rem',
      boxShadow: 'none',
      backgroundColor: '#fff',
      color: '#6639F4',
      border: '1px solid #6639F4',
      textTransform: 'uppercase',
      fontWeight: 600,
      fontSize: '0.875rem',
      borderRadius: '8px',
    },
  }

  switch (providerId) {
    case FACEBOOK:
      return (
        <FacebookLoginButton
          onClick={() => (window.location.href = loginUrl)}
          text={displayName}
          iconColor={'#3b5998'}
          {...config}
        />
      )
    case GITHUB:
      return <GithubLoginButton onClick={() => (window.location.href = loginUrl)} />
    case GOOGLE:
      return (
        <GoogleLoginButton
          onClick={() => (window.location.href = loginUrl)}
          text={displayName}
          {...config}
        />
      )
    default:
      return null
  }
}
